export default {
	data() {
		return {
			swiperList:null,
			userPoint: null,
			goods: null,
			coupons: [],
			coupon_month:[],
			coupon_day:[],
			page:1
		}
	},
	onLoad() {
		
	},
	onShow() {
		this.getPoints()
		this.getAd()
		this.getGoodsList()
		this.getCouponList()
	},
	methods: {
		toMoreCoupons(type){
			uni.setStorageSync("coupons",this.coupons)
			uni.navigateTo({
				url:"../moreCoupons/moreCoupons?type="+type
			})
		},
		toMore(){
			uni.navigateTo({
				url:"../morePointGoods/morePointGoods"
			})
		},
		toGetCoupon(id){
			uni.navigateTo({
				url: "../couponDetail/couponDetail?id=" + id+"&type=free"
			})
		},
		toCouponDetail(id){
			uni.navigateTo({
				url: "../couponDetail/couponDetail?id=" + id
			})
		},
		toDetail(url) {
			console.log(url)
			uni.navigateTo({
				url: url
			})
		},
		toGoodsDetail(id) {
			uni.navigateTo({
				url: "../integralGoodsDetail/integralGoodsDetail?id=" + id
			})
		},
		getAd() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/Ad/index',
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.swiperList = res.data.data
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		toMyIntegraOrders() {
			uni.navigateTo({
				url: '../myIntegraOrder/myIntegraOrder'
			})
		},
		getCouponList() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/Coupon/indexCouponList',
				data: {
					token: uni.getStorageSync('token')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.coupons = res.data.data.coupon_pay
						this.coupon_month = res.data.data.coupon_month
						this.coupon_day = res.data.data.coupon_day
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		getGoodsList() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/goods/indexIntegralMall',
				data: {
					token: uni.getStorageSync('token'),
					type: 0,
					page: this.page,
					offset: 10
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.goods = res.data.data[0]
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		getPoints() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/userCenter/getScore',
				data: {
					token: uni.getStorageSync('token')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.userPoint = parseFloat(res.data.data.pay_points)
						return
					}
					this.showToast(res.data.msg)
				}
			})
		}
	}
}
